<template>
  <div class="table-wrapper">
    <div class="table-min">
      <table class="file-table">
        <thead>
          <tr>
            <th><input type="checkbox" @change="$emit('toggle-all', $event)" :checked="isAllChecked" /></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-if="isLoading">
            <td colspan="6" class="loading-cell">...</td>
          </tr>
          <tr v-else-if="files.length === 0">
            <td colspan="6" class="empty-cell">
              {{ isSearchMode ? '' : '' }}
            </td>
          </tr>
          <tr v-else v-for="file in files" :key="file.name + file.type">
            <td><input type="checkbox" :checked="selectedFiles.includes(getFileId(file))" @change="handleFileSelect(file, $event)" /></td>
            <td @dblclick="handleDoubleClick(file)">
              <div class="file-name-cell">
                <i :class="getFileIcon(file)" class="file-icon"></i>
                <span>{{ file.name }}</span>
              </div>
            </td>
            <td>{{ file.type === 'folder' ? '' : getFileType(file.extension) }}</td>
            <td>{{ formatTime(file.modified) }}</td>
            <td>{{ file.type === 'folder' ? '-' : formatFileSize(file.size) }}</td>
            <td>
              <button class="action-btn" @click="$emit('rename', file)"></button>
              <button class="action-btn" @click="$emit('move', file)"></button>
              <button v-if="file.type === 'file'" class="action-btn" @click="$emit('download', file)"></button>
              <button class="action-btn delete-btn" @click="$emit('delete', file)"></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script setup>
/* eslint-disable no-undef */
import { computed } from 'vue';

const props = defineProps({
  files: {
    type: Array,
    default: () => []
  },
  selectedFiles: {
    type: Array,
    default: () => []
  },
  isLoading: {
    type: Boolean,
    default: false
  },
  isSearchMode: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['update:selectedFiles', 'toggle-all', 'rename', 'move', 'download', 'delete', 'enter-folder', 'preview']);

const isAllChecked = computed(() => {
  return props.files.length > 0 && props.selectedFiles.length === props.files.length;
});

// 
const handleDoubleClick = (file) => {
  if (file.type === 'folder') {
    emit('enter-folder', file);
  } else if (file.type === 'file') {
    // 
    emit('preview', file);
  }
};

// ID
const getFileId = (file) => {
  return `${file.type}_${file.name}_${file.modified}`;
};

// 
const getFileIcon = (file) => {
  if (file.type === 'folder') {
    return 'fas fa-folder';
  }
  
  const ext = file.extension?.toLowerCase();
  const iconMap = {
    'pdf': 'fas fa-file-pdf',
    'doc': 'fas fa-file-word',
    'docx': 'fas fa-file-word',
    'xls': 'fas fa-file-excel',
    'xlsx': 'fas fa-file-excel',
    'ppt': 'fas fa-file-powerpoint',
    'pptx': 'fas fa-file-powerpoint',
    'jpg': 'fas fa-file-image',
    'jpeg': 'fas fa-file-image',
    'png': 'fas fa-file-image',
    'gif': 'fas fa-file-image',
    'mp4': 'fas fa-file-video',
    'avi': 'fas fa-file-video',
    'mp3': 'fas fa-file-audio',
    'wav': 'fas fa-file-audio',
    'zip': 'fas fa-file-archive',
    'rar': 'fas fa-file-archive',
    'txt': 'fas fa-file-alt',
    'md': 'fas fa-file-alt'
  };
  
  return iconMap[ext] || 'fas fa-file';
};

// 
const getFileType = (extension) => {
  if (!extension) return '';
  
  const typeMap = {
    'pdf': 'PDF',
    'doc': 'Word',
    'docx': 'Word',
    'xls': 'Excel',
    'xlsx': 'Excel',
    'ppt': 'PPT',
    'pptx': 'PPT',
    'jpg': '',
    'jpeg': '',
    'png': '',
    'gif': '',
    'mp4': '',
    'avi': '',
    'mp3': '',
    'wav': '',
    'zip': '',
    'rar': '',
    'txt': '',
    'md': 'Markdown'
  };
  
  return typeMap[extension.toLowerCase()] || extension.toUpperCase() + '';
};

// 
const formatFileSize = (size) => {
  if (!size || size === 0) return '0 B';
  const i = Math.floor(Math.log(size) / Math.log(1024));
  return (size / Math.pow(1024, i)).toFixed(2) + ' ' + ['B', 'KB', 'MB', 'GB'][i];
};

const formatTime = (timestamp) => {
  return new Date(timestamp * 1000).toLocaleString('zh-CN');
};

// 
const handleFileSelect = (file, event) => {
  const fileId = getFileId(file);
  const isChecked = event.target.checked;
  
  let newSelectedFiles;
  if (isChecked) {
    newSelectedFiles = [...props.selectedFiles, fileId];
  } else {
    newSelectedFiles = props.selectedFiles.filter(id => id !== fileId);
  }
  
  emit('update:selectedFiles', newSelectedFiles);
};
</script>
