<template>
  <div class="pagination-container" v-if="pagination && pagination.total_pages > 1">
    <span class="page-info">
       {{ pagination.page }}  {{ pagination.total_pages }}  ({{ pagination.total }} )
    </span>
    
    <div class="pagination">
      <button :disabled="pagination.page === 1" @click="$emit('change-page', pagination.page - 1)">
        <i class="fas fa-chevron-left"></i> 
      </button>
      
      <!--  -->
      <template v-for="page in pageNumbers" :key="page">
        <button 
          v-if="page !== '...'"
          :class="{ 'active': page === pagination.page }" 
          @click="$emit('change-page', page)"
        >
          {{ page }}
        </button>
        <span v-else class="page-ellipsis">...</span>
      </template>
      
      <button :disabled="pagination.page === pagination.total_pages" @click="$emit('change-page', pagination.page + 1)">
         <i class="fas fa-chevron-right"></i>
      </button>
    </div>
  </div>
</template>

<script setup>
/* eslint-disable no-undef, no-unused-vars */
import { computed } from 'vue';

const props = defineProps({
  pagination: {
    type: Object,
    default: null
  }
});

const emit = defineEmits(['change-page']);

// 
const pageNumbers = computed(() => {
  if (!props.pagination) return [];
  
  const { page, total_pages } = props.pagination;
  const pages = [];
  
  if (total_pages <= 7) {
    // 7
    for (let i = 1; i <= total_pages; i++) {
      pages.push(i);
    }
  } else {
    // 7
    if (page <= 4) {
      // 
      pages.push(1, 2, 3, 4, 5, '...', total_pages);
    } else if (page >= total_pages - 3) {
      // 
      pages.push(1, '...', total_pages - 4, total_pages - 3, total_pages - 2, total_pages - 1, total_pages);
    } else {
      // 
      pages.push(1, '...', page - 1, page, page + 1, '...', total_pages);
    }
  }
  
  return pages;
});
</script>
