<template>
  <div class="file-list-container">
    <!--  -->
    <div class="file-card">
      <div class="search-and-batch-actions">
        <div class="left-section">
          <h3></h3>
        </div>
        <div class="right-section">
          <div class="action-buttons">
            <button 
              v-if="canGoBack" 
              @click="goBack" 
              class="batch-btn"
            >
              <i class="fas fa-arrow-left"></i> 
            </button>
            <button @click="refreshDirectory" class="batch-btn">
              <i class="fas fa-sync-alt"></i> 
            </button>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="path-info">
        <div class="current-path">
          <span @click="navigateToPath('')" class="path-link">
            <i class="fas fa-database"></i> data
          </span>
          <template v-if="currentPath">
            <span v-for="(segment, index) in pathSegments" :key="index">
              <span class="path-separator"> / </span>
              <span 
                @click="navigateToPath(pathSegments.slice(0, index + 1).join('/'))" 
                class="path-link"
              >
                {{ segment }}
              </span>
            </span>
          </template>
        </div>
      </div>

      <!--  -->
      <div class="table-wrapper">
        <div class="table-min">
          <table class="file-table">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-if="loading">
                <td colspan="5" class="loading-cell">...</td>
              </tr>
              <tr v-else-if="displayFiles.length === 0">
                <td colspan="5" class="empty-cell">
                  
                </td>
              </tr>
              <tr v-else v-for="file in displayFiles" :key="file.name + file.type">
                <td @dblclick="handleDoubleClick(file)">
                  <div class="file-name-cell">
                    <i :class="getFileIcon(file)" class="file-icon"></i>
                    <span>{{ file.name }}</span>
                  </div>
                </td>
                <td>{{ file.type === 'folder' ? '' : getFileType(file.extension) }}</td>
                <td>{{ formatTime(file.modified) }}</td>
                <td>{{ file.type === 'folder' ? '-' : formatFileSize(file.size) }}</td>
                <td>
                  <button v-if="file.type === 'file'" class="action-btn" @click="handleDownload(file)"></button>
                  <button class="action-btn delete-btn" @click="handleDelete(file)"></button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <!--  -->
      <div class="pagination-container" v-if="pagination && pagination.total_pages > 1">
        <span class="page-info">
           {{ pagination.page }}  {{ pagination.total_pages }}  ({{ pagination.total }} )
        </span>
        
        <div class="pagination">
          <button :disabled="pagination.page === 1" @click="handleCurrentChange(pagination.page - 1)">
            <i class="fas fa-chevron-left"></i> 
          </button>
          
          <!--  -->
          <template v-for="page in pageNumbers" :key="page">
            <button 
              v-if="page !== '...'"
              :class="{ 'active': page === pagination.page }" 
              @click="handleCurrentChange(page)"
            >
              {{ page }}
            </button>
            <span v-else class="page-ellipsis">...</span>
          </template>
          
          <button :disabled="pagination.page === pagination.total_pages" @click="handleCurrentChange(pagination.page + 1)">
             <i class="fas fa-chevron-right"></i>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, inject } from 'vue';
import { ensureVisitJwt, getVisitJwt } from '@/utils/auth.js';

// 
const notification = inject('notification');
const confirm = inject('confirm');

// 
const loading = ref(false);
const fileList = ref([]);
const currentPath = ref('');
const pagination = ref({
  page: 1,
  per_page: 20,
  total: 0,
  total_pages: 0,
  has_prev: false,
  has_next: false
});

// 
const pathSegments = computed(() => {
  return currentPath.value ? currentPath.value.split('/').filter(Boolean) : [];
});

const canGoBack = computed(() => {
  return currentPath.value !== '';
});

const displayFiles = computed(() => {
  return fileList.value.map(item => ({
    ...item,
    type: item.is_file ? 'file' : 'folder',
    extension: item.is_file ? getFileExtension(item.name) : null,
    size: item.size || 0,
    modified: item.modified || Date.now() / 1000
  }));
});

// 
const pageNumbers = computed(() => {
  if (!pagination.value) return [];
  
  const { page, total_pages } = pagination.value;
  const pages = [];
  
  if (total_pages <= 7) {
    // 7
    for (let i = 1; i <= total_pages; i++) {
      pages.push(i);
    }
  } else {
    // 7
    if (page <= 4) {
      // 
      pages.push(1, 2, 3, 4, 5, '...', total_pages);
    } else if (page >= total_pages - 3) {
      // 
      pages.push(1, '...', total_pages - 4, total_pages - 3, total_pages - 2, total_pages - 1, total_pages);
    } else {
      // 
      pages.push(1, '...', page - 1, page, page + 1, '...', total_pages);
    }
  }
  
  return pages;
});

// 
const getFileExtension = (filename) => {
  const lastDot = filename.lastIndexOf('.');
  return lastDot > 0 ? filename.substring(lastDot + 1).toLowerCase() : '';
};

const getItemPath = (item) => {
  if (currentPath.value) {
    return `${currentPath.value}/${item.name}`;
  }
  return item.name;
};

// 
const getFileIcon = (file) => {
  if (file.type === 'folder') {
    return 'fas fa-folder';
  }
  
  const ext = file.extension?.toLowerCase();
  const iconMap = {
    'pdf': 'fas fa-file-pdf',
    'doc': 'fas fa-file-word',
    'docx': 'fas fa-file-word',
    'xls': 'fas fa-file-excel',
    'xlsx': 'fas fa-file-excel',
    'ppt': 'fas fa-file-powerpoint',
    'pptx': 'fas fa-file-powerpoint',
    'jpg': 'fas fa-file-image',
    'jpeg': 'fas fa-file-image',
    'png': 'fas fa-file-image',
    'gif': 'fas fa-file-image',
    'mp4': 'fas fa-file-video',
    'avi': 'fas fa-file-video',
    'mp3': 'fas fa-file-audio',
    'wav': 'fas fa-file-audio',
    'zip': 'fas fa-file-archive',
    'rar': 'fas fa-file-archive',
    'txt': 'fas fa-file-alt',
    'md': 'fas fa-file-alt'
  };
  
  return iconMap[ext] || 'fas fa-file';
};

// 
const getFileType = (extension) => {
  if (!extension) return '';
  
  const typeMap = {
    'pdf': 'PDF',
    'doc': 'Word',
    'docx': 'Word',
    'xls': 'Excel',
    'xlsx': 'Excel',
    'ppt': 'PPT',
    'pptx': 'PPT',
    'jpg': '',
    'jpeg': '',
    'png': '',
    'gif': '',
    'mp4': '',
    'avi': '',
    'mp3': '',
    'wav': '',
    'zip': '',
    'rar': '',
    'txt': '',
    'md': 'Markdown'
  };
  
  return typeMap[extension.toLowerCase()] || extension.toUpperCase() + '';
};

// 
const formatFileSize = (size) => {
  if (!size || size === 0) return '0 B';
  const i = Math.floor(Math.log(size) / Math.log(1024));
  return (size / Math.pow(1024, i)).toFixed(2) + ' ' + ['B', 'KB', 'MB', 'GB'][i];
};

const formatTime = (timestamp) => {
  return new Date(timestamp * 1000).toLocaleString('zh-CN');
};

// 
const loadDirectory = async (path = '', page = 1) => {
  loading.value = true;
  
  try {
    // JWT
    const hasJwt = await ensureVisitJwt();
    if (!hasJwt) {
      notification.open({
        type: 'error',
        title: '',
        message: '',
        onClose: () => window.location.reload()
      });
      return;
    }

    const jwt = getVisitJwt();
    const response = await fetch('/api/file/admin-file-api.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${jwt}`
      },
      body: JSON.stringify({
        action: 'browse_directory',
        path: path,
        page: page,
        per_page: pagination.value.per_page
      })
    });

    const result = await response.json();
    
    if (result.success) {
      fileList.value = result.data.items || [];
      pagination.value = {
        page: result.data.pagination?.page || 1,
        per_page: result.data.pagination?.per_page || 20,
        total: result.data.pagination?.total || 0,
        total_pages: result.data.pagination?.total_pages || 0,
        has_prev: result.data.pagination?.has_prev || false,
        has_next: result.data.pagination?.has_next || false
      };
      currentPath.value = path;
    } else {
      notification.open({
        type: 'error',
        title: '',
        message: result.message || ''
      });
    }
  } catch (error) {
    console.error(':', error);
    notification.open({
      type: 'error',
      title: '',
      message: ''
    });
  } finally {
    loading.value = false;
  }
};

// 
const navigateToPath = (path) => {
  loadDirectory(path, 1);
};

const goBack = () => {
  const segments = pathSegments.value;
  if (segments.length > 0) {
    const parentPath = segments.slice(0, -1).join('/');
    navigateToPath(parentPath);
  }
};

const refreshDirectory = () => {
  loadDirectory(currentPath.value, pagination.value.page);
};

// 
const handleCurrentChange = (page) => {
  loadDirectory(currentPath.value, page);
};
  
// 
const handleDoubleClick = (file) => {
  // 
  if (file.type === 'folder') {
    const newPath = currentPath.value ? `${currentPath.value}/${file.name}` : file.name;
    navigateToPath(newPath);
  }
  // 
};

const handleDelete = async (file) => {
  confirm.open({
    title: '',
    message: `${file.type === 'folder' ? '' : ''}"${file.name}"${file.type === 'folder' ? '' : ''}`,
    onConfirm: async () => {
      try {
        // JWT
        const hasJwt = await ensureVisitJwt();
        if (!hasJwt) {
          notification.open({
            type: 'error',
            title: '',
            message: ''
          });
          return;
        }

        const jwt = getVisitJwt();
        const response = await fetch('/api/file/admin-file-api.php', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${jwt}`
          },
          body: JSON.stringify({
            action: 'delete_file',
            file_path: getItemPath(file)
          })
        });

        const result = await response.json();
        
        if (result.success) {
          notification.open({
            type: 'success',
            title: '',
            message: `${file.type === 'folder' ? '' : ''}`,
            duration: 3000
          });
          refreshDirectory();
        } else {
          notification.open({
            type: 'error',
            title: '',
            message: result.message || ''
          });
        }
      } catch (error) {
        console.error(':', error);
        notification.open({
          type: 'error',
          title: '',
          message: ''
        });
      }
    }
  });
};

const handleDownload = async (file) => {
  try {
    // JWT
    const hasJwt = await ensureVisitJwt();
    if (!hasJwt) {
      notification.open({
        type: 'error',
        title: '',
        message: ''
      });
      return;
    }

    const jwt = getVisitJwt();
    
    // URL
    const params = new URLSearchParams();
    params.append('action', 'download_file');
    params.append('file_path', getItemPath(file));
    params.append('jwt', jwt);
    
    const downloadUrl = `/api/file/admin-file-api.php?${params.toString()}`;
    
    // a
    const link = document.createElement('a');
    link.href = downloadUrl;
    link.download = file.name;
    link.style.display = 'none';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  } catch (error) {
    console.error(':', error);
    notification.open({
      type: 'error',
      title: '',
      message: ''
    });
  }
};

// 
onMounted(() => {
  loadDirectory('', 1);
});
</script>

<style>
@import './file-list.css';
</style>